/**
 * 作業回圈ViewModel
 *
 * @public
 * @class JobLoopViewModel
 */
export class JobLoopViewModel {
  /// <summary>
  ///  迴圈序號
  /// <summary>
  public loop_seq: number;
  /// <summary>
  ///  SSIS作業編號
  /// <summary>
  public ssis_job_no: string;
  /// <summary>
  ///  作業迴圈欄位一
  /// <summary>
  public job01: string;
  /// <summary>
  ///  作業迴圈欄位二
  /// <summary>
  public job02: string;
  /// <summary>
  ///  作業迴圈欄位三
  /// <summary>
  public job03: string;
  /// <summary>
  ///  執行順序
  /// <summary>
  public exec_seq: number;
  /// <summary>
  ///  啟用註記
  /// <summary>
  public is_active: boolean;
  /// <summary>
  ///  作業迴圈說明
  /// <summary>
  public job_loop_desc: string;
  /// <summary>
  ///  遠端連線編號
  /// <summary>
  public remote_conn_id: string;
  /// <summary>
  ///  最後執行時間
  /// <summary>
  public last_exec_date: Date;
  /// <summary>
  ///  最後執行狀態主鍵
  /// <summary>
  public last_exec_status_key: string;
  /// <summary>
  ///  建立者
  /// <summary>
  public creator: string;
  /// <summary>
  ///  建立時間
  /// <summary>
  public create_time: Date;
  /// <summary>
  ///  最後更新者
  /// <summary>
  public modifier: string;
  /// <summary>
  ///  最後更新時間
  /// <summary>
  public last_update_time: Date;

  public last_exec_status_desc: string;

  public last_exec_status_color: string;

  public is_active_color: string;
  public is_active_desc: string;
  public end_time: string;
  public duration: string;
   /// <summary>
  ///  作業替換迴圈說明
  /// <summary>
  public job_loop_replace_desc: string;
  public pwd:string;

    /// <summary>
  ///  遠端連線字串
  /// <summary>
  public remote_conn_string: string;
}



